
@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');


*{
    margin: 0;
    font-family : "Plus Jakarta Sans", sans-serif ; 
    color: #333;
}


.navbar{
    padding: 20px;
    display: flex;
    align-items:center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom:  1px solid #f2f2f2;

}

.navbar h1 {
   color: #f1356d;
}

.navbar .links{
    margin-left: auto;
    
}



.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navbar a:hover {
color:#f1356d;
}

.content{
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}

.button{
    border:1px solid rgb(173, 172, 172);
    border-radius: 5px;
    margin-left:5px;
}

.blog{
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid #fafafa;
}

.blog:hover{
    box-shadow:1px 3px 5px rgba(0,0,0,0.1);
}

.blog h2{
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 8px;
}

button{
    border: 1px solid rgb(202, 200, 200);
    border-radius: 5px;
}